<template>
  <div class="navlist">
      <span class="navlist-content">{{ option[index].name }}</span>
      <span v-show="flag" class="navlist-open" @click="changeFlag">▼</span>
      <span v-show="!flag" class="navlist-open" @click="changeFlag">▲</span>
      <ul class="navlist-ul" v-show="!flag">
        <li class="navlist-li" v-for="(op,i) in option" @click="changeIndex(i)">{{ op.name }}</li>
      </ul>
  </div>
</template>

<script>
export default {
  name: "Navlist",
  data(){
    return {
      flag: true,
      index:this.def
    }
  },
  props: {
    option:{
      type: Array,
      default: function(){
        return [{name:"无",value:0}]
      }
    },
    def:{
      type:Number,
      default: 0
    }
  },
  methods: {
    changeFlag(){
      this.flag = !this.flag
    },
    changeIndex(i){
      this.index = i;
      this.flag = true;
      this.$emit("updata_navlist",this.index);
    }
  },
  mounted(){
    this.$emit("updata_navlist",this.index);
  }
}
</script>

<style scoped>
.navlist {
  display: inline-block;
  text-indent: 0;
  background-color: #fff;
  border: 1px solid #999;
  width:7.1rem;
  height: 2.0rem;
  line-height: 2.0rem;
  text-indent: 0.8rem;
  position: relative;
  user-select: none;
}
.navlist-open{
  display:inline-block;
  background-color: #ddd;
  width: 1.9rem;
  height: 2.0rem;
  line-height: 2.3rem;
  text-indent:0;
  text-align: center;
  position: absolute;
  right:0;
  cursor:pointer;
  border-left: 1px solid #999;
}
.navlist-open:hover,.navlist-li:hover{
 background-color: rgb(8,165,99);
 color:#fff;
}
.navlist-ul{
  text-indent: 0;
  position:absolute;
  left:-0.05rem;
  border: 1px solid #999;
}
.navlist-li{
  width:7.1rem;
  height: 2.0rem;
  text-align: center;
  background-color: #fff;
}
</style>



