<template>
  <dir class="checkboxs">
    <span v-for="(n,i) in option" @click="changecheckednum(i)" :class="{activespan:i==index}">{{ n.name }}</span>
  </dir>
</template>

<script>
export default {
  name:"Checkboxs",
  data(){
    return {
      index:this.def
    }
  },
  props:{
    def:{
      type:Number,
      default:0
    },
    option:{
      type:Array,
      default:function(){
        return [{name:"无",value:0}]
      }
    }
  },
  methods: {
    changecheckednum(i){
      this.index = i;
      this.$emit("updata_checkboxs",this.index);
    }
  },
  mounted(){
    this.$emit("updata_checkboxs",this.index);
  }
}
</script>

<style scoped>
.checkboxs {
  display: inline-block;
  text-indent: 0;
  margin: 0;
  padding: 0;
}
.checkboxs span {
  display: inline-block;
  background-color: #fff;
  margin: 0 0.5rem 0 0;
  height: 2rem;
  width: 3.5rem;
  line-height: 2rem;
  text-align: center;
  border: 1px solid #999;
  user-select: none;
  cursor: pointer;
}
.checkboxs span:hover,.checkboxs .activespan {
  background-color: rgb(8, 165, 99);
  color: #fff;
}
</style>



