<template>
  <div calss="index-wrap">
    <div class="index-left">
      <div class="index-left-block">
        <h2>推荐产品</h2>
        <template v-for="(product, index) in productList">
          <h3>{{ product.category }}</h3>
          <ul>
            <li v-for="item in product.list">
              <a :href="item.url" target="_blank">{{ item.name }}</a>
              <span v-if="item.hot">HOT</span>
            </li>
          </ul>
          <div v-if="index%2 === 0" class="hr"></div>
        </template>
      </div>
      <div class="index-left-block">
        <h2>使用Axios跨域get消息</h2>
          <ul>
            <li v-for="n in news">
              <a :href="n.url" target="_blank" :title="n['author_name'] + ' | ' + n.date">{{ n.title }}</a>
              <span v-if="n.hot">HOT</span>
            </li>
          </ul>
      </div>
    </div>
    <div class="index-right">
      <div class="index-right-block">
        <swiper :options="swiperOption">
          <!-- slides -->
          <swiper-slide v-for='n in displayImg'>
            <a href="http://www.baidu.com" target="_blank">
              <img :src="n.src" :alt="n.name" class="swiperimg">
            </a>
          </swiper-slide>
          <!-- Optional controls -->
          <div class="swiper-pagination"  slot="pagination"></div>
          <div class="swiper-button-prev" slot="button-prev"></div>
          <div class="swiper-button-next" slot="button-next"></div>
        </swiper>
      </div>
      <div class="index-right-blocks">
        <template v-for="btn in buttons">
          <div class="index-right-blocks-box">
            <div :class="'index-right-blocks-box-inner index-right-blocks-box-inner-' + btn.name">
              <h2>{{ btn.title }}</h2>
              <span>{{ btn.content }}</span>
              <router-link :to="'/details/' + btn.name " tag="span" class="index-right-blocks-box-inner-btn">立即购买</router-link>
            </div>
          </div>
        </template>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: "layout",
  data() {
    return {
      productList: [
        {
          category: "大BOSS",
          list: [
            {
              name: "腾讯",
              url: "http://www.qq.com",
              hot: true
            },
            {
              name: "阿里",
              url: "http://www.taobao.com",
              hot: false
            },
            {
              name: "网易",
              url: "http://www.163.com",
              hot: false
            }
          ]
        },
        {
          category: "前端工程师必学！",
          list: [
            {
              name: "JavaScript",
              url: "http://www.w3school.com.cn/js/index.asp",
              hot: false
            },
            {
              name: "Vue.js",
              url: "https://cn.vuejs.org/",
              hot: true
            },
            {
              name: "Node.js",
              url: "http://nodejs.cn/",
              hot: false
            },
            {
              name: "Vue.js原理",
              url: "https://cn.vuejs.org/",
              hot: true
            },
            {
              name: "Node.js原理",
              url: "http://nodejs.cn/",
              hot: false
            }
          ]
        }
      ],
      news: [],
      displayImg: [
        {
          name: "黑人",
          src: require("../assets/slideShow/1.jpg")
        },
        {
          name: "金毛",
          src: require("../assets/slideShow/2.jpg")
        },
        {
          name: "水潭",
          src: require("../assets/slideShow/3.jpg")
        },
        {
          name: "汽车",
          src: require("../assets/slideShow/4.jpg")
        }
      ],
      buttons: [
        {
          name: "sandwich",
          title: "三明治",
          content: "有蛋蛋更好吃~"
        },
        {
          name: "coffee",
          title: "一杯咖啡",
          content: "温暖你我，苦到不行。"
        },
        {
          name: "salad",
          title: "水果沙拉",
          content: "健健康康一辈子"
        },
        {
          name: "hamburger",
          title: "双层牛肉堡",
          content: "超级堡，超级饱。"
        }
      ],
      swiperOption: {
        pagination: {
          el: ".swiper-pagination"
        },
        loop: true,
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        autoplay: true
      }
    };
  },
  created() {
    this.$axios
      .get("http://wwtliu.com/sxtstu/news/juhenews.php", {
        params: {
          count: 10,
          type: "top"
        }
      })
      .then(res => {
        if (res.data.result === null) {
          this.news.push({
            title: "超过每日可允许请求次数!",
            author_name: "Liu_lihao",
            date: new Date()
          });
        } else {
          this.news = res.data.result.data.filter(function(item, index) {
            return index < 10;
          });
        }
      })
      .catch(error => {
        console.log(error);
      });
  }
};
</script>

<style>
/* 左边 */
.index-left {
  display: inline-block;
  width: 29%;
  height: 800px;
}
.index-left-block {
  width: 85%;
  height: 333px;
  margin: 0 auto;
  margin-bottom: 20px;
  /* padding-bottom: 15px; */
  background-color: rgb(240, 240, 240);
  box-shadow: 1px 2px 14px rgb(205, 209, 218);
}
.index-left-block h2 {
  font-size: 18px;
  height: 40px;
  line-height: 40px;
  text-indent: 16px;
  color: #fff;
  background-color: rgb(8, 165, 99);
  cursor: default;
  margin-bottom: 15px;
}
.index-left-block h3 {
  font-size: 16px;
  padding: 0 10px 0 20px;
  cursor: default;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.index-left-block ul {
  padding: 1px 10px 10px 15px;
}
.index-left-block li {
  font-size: 14px;
  margin: 6px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.index-left-block span {
  background-color: red;
  color: #fff;
}
.index-left-block .hr {
  height: 2px;
  background-color: #ddd;
  margin-bottom: 15px;
}
/* 右边 */
.index-right {
  float: right;
  width: 70%;
  height: 800px;
}
.index-right .index-right-block {
  width: 95%;
  box-shadow: 1px 2px 14px rgb(205, 209, 218);
}
.index-right .swiperimg {
  width: 100%;
  min-width: 95%;
  height: 333px;
}
.index-right-blocks {
  margin-top: 20px;
  width: 100%;
}
.index-right-blocks-box {
  width: 50%;
  display: inline-block;
  margin-bottom: 20px;
}
.index-right-blocks-box-inner {
  width: 90%;
  height: 156px;
  background-color: rgb(240, 240, 240);
  /* background-image: url("../assets/images/1.png"); */
  background-repeat: no-repeat;
  background-position: 10% 70%;
  position: relative;
  box-shadow: 1px 2px 14px rgb(205, 209, 218);
}
.index-right-blocks-box-inner-sandwich {
  background-image: url("../assets/images/1.png");
}
.index-right-blocks-box-inner-coffee {
  background-image: url("../assets/images/2.png");
}
.index-right-blocks-box-inner-salad {
  background-image: url("../assets/images/3.png");
}
.index-right-blocks-box-inner-hamburger {
  background-image: url("../assets/images/4.png");
}
.index-right-blocks-box h2 {
  position: absolute;
  top: 15%;
  left: 45%;
  font-size: 18px;
  height: 40px;
  line-height: 40px;
  cursor: default;
}
.index-right-blocks-box span {
  position: absolute;
  top: 34%;
  left: 45%;
  font-size: 16px;
  height: 40px;
  line-height: 40px;
  cursor: default;
}
.index-right-blocks-box .index-right-blocks-box-inner-btn {
  position: absolute;
  top: 60%;
  left: 45%;
  display: block;
  width: 85px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  background-color: rgb(8, 165, 99);
  font-size: 17px;
  font-weight: bold;
  color: #fff;
  border-radius: 3px;
  cursor: pointer;
}
</style>



