<template>
  <div class="details-wrap">
    <div class="details-nav">
      <div :class="'details-nav-inner details-nav-inner-' + getUrl">
        <ul>
           <router-link v-for="n in navList" :to="{ path : '/details/'+n.name}" tag="li" active-class="details-nav-inner-li-active">
             {{ n.title }}
           </router-link>
        </ul>
      </div>
    </div>
    <div class="details-content">
      <div class="details-content-inner">
        <keep-alive>
         <router-view />
        </keep-alive>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "detailss",
  data() {
    return {
      navList: [
        {
          name: "sandwich",
          title: "三明治",
          content: "有蛋蛋更好吃~"
        },
        {
          name: "coffee",
          title: "一杯咖啡",
          content: "温暖你我，苦到不行。"
        },
        {
          name: "salad",
          title: "水果沙拉",
          content: "健健康康一辈子"
        },
        {
          name: "hamburger",
          title: "双层牛肉堡",
          content: "超级堡，超级饱。"
        }
      ]
    };
  },
  computed: {
    getUrl() {
      return this.$route.path.replace("/details/","")
    }
  }
};
</script>

<style>
.details-nav {
  width: 29%;
  display: inline-block;
}
.details-nav-inner {
  width: 85%;
  height: 333px;
  background-color: rgb(240, 240, 240);
  margin: 0 auto;
  background-repeat: no-repeat;
  background-position: 50% 10%;
  text-align: center;
  box-shadow:1px 2px 14px rgb(205, 209, 218);
}
.details-nav-inner-sandwich {
  background-image: url("../assets/images/1.png");
}
.details-nav-inner-coffee {
  background-image: url("../assets/images/2.png");
}
.details-nav-inner-salad {
  background-image: url("../assets/images/3.png");
}
.details-nav-inner-hamburger {
  background-image: url("../assets/images/4.png");
}
.details-nav-inner ul {
  padding-top: 160px;
}
.details-nav-inner li {
  height: 35px;
  line-height: 35px;
  margin: 0 35px;
  font-size: 17px;
  font-weight: bold;
  letter-spacing: 4px;
  cursor: pointer;
  overflow: hidden;
}
.details-nav-inner li:hover, .details-nav-inner-li-active {
  color: #fff;
  background-color: rgb(8, 165, 99);
  box-shadow:1px 2px 14px rgb(205, 209, 218);
}
.details-content {
  width: 70%;
  float: right;
}
.details-content-inner {
  width: 95%;
}
</style>



