import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/pages/layout'
import Details from '@/pages/details'
import Sandwich from "@/pages/details/sandwich"
import Coffee from "@/pages/details/coffee"
import Salad from "@/pages/details/salad"
import Hamburger from "@/pages/details/hamburger"
Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'Layout',
    base:"/hot/",
    component: Layout
  }, {
    path: '/details',
    name: 'Details',
    component: Details,
    redirect: "/details/sandwich",
    children: [{
      path: '/details/sandwich',
      name: 'Sandwich',
      component: Sandwich
    }, {
      path: '/details/coffee',
      name: 'Coffee',
      component: Coffee
    }, {
      path: '/details/salad',
      name: 'Salad',
      component: Salad
    }, {
      path: '/details/hamburger',
      name: 'Hamburger',
      component: Hamburger
    }]
  }]
})



