import Vue from "vue"
import Vuex from "vuex"
import manygoods from "./manygoods.js"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    //和Vue实例的data一样，存放内容(变量)
    //调用方法
    //在视图层中：$store.state.title
    //在逻辑层中：this.$store.state.title 下同~
    title: "今天的商品价格表",
    goods: [
      {name:"三明治",value:30,id:"三明治"},
      {name:"咖啡",value:50,id:"咖啡"},
      {name:"水果沙拉",value:80,id:"水果沙拉"},
      {name:"汉堡",value:100,id:"汉堡"}
     ],
     addName: "☆金牌——"
  },
  getters : {
    //和Vue实例的computed一样，放置需计算的变量。
    //调用方法：在视图层：$store.getters.bazhe
    bazhe(state){
      return state.goods.map(function(good){
        return {name: good.name,value: good.value*0.8}
      })
    },
    allGoods(state){
      let allgoods = state.goods.map(i=>i);
      state.manyGoods.manygoods.forEach(i=>{
        allgoods.push(i);
      });
      return allgoods
    }
  },
  mutations: {
    //和Vue实例的methods一样，放置方法
    //调用方法：在视图层：$store.commit("changegoodsname",payload)
    changegoodsname(state,payload){
      console.log("来自" + payload + "页面的修改");
      state.goods.forEach(function(good,index){
        state.goods[index].name = state.addName + state.goods[index].id;
      });
    }
  },
  actions : {
    //由于mutations只能同步，于是actions孕育而生，管理mutations，且是异步的。
    //调用方法：在视图层：$store.dispatch("changegoodsname",payload)
    changegoodsname(state,payload){
      console.log("来自" + payload + "页面触发的actions的changegoodsname");
      state.commit('changegoodsname','actions')
    }
  },
  modules: {
    //模块化开发，将manyGoods置于模块内容指定的类型下。
    //manygoods 是from "./manygoods.js"
    //export default{ state:{ goods:[...] } } 在state下
    manyGoods: manygoods
  }
})
//区分 actions 和 mutations 并不是为了解决竞态问题，而是为了能用 devtools 追踪状态变化。
//事实上在 vuex 里面 actions 只是一个架构性的概念，并不是必须的，说到底只是一个函数，你在里面想干嘛都可以，只要最后触发 mutation 就行。异步竞态怎么处理那是用户自己的事情。
//vuex 真正限制你的只有 mutation 必须是同步的这一点



