<template>
  <div>
    coffee
    {{ $store.state.title }}
    <table border="1">
      <tr>
        <td>名称</td>
        <td>价格</td>
      </tr>
      <tr v-for="good in $store.state.goods">
        <td>{{ good.name }}</td>
        <td>{{ good.value }}</td>
      </tr>
    </table>
    <hr>
    {{ $store.state.title }}（VIP用户）
    <table border="1">
      <tr>
        <td>名称</td>
        <td>价格</td>
      </tr>
      <tr v-for="good in $store.getters.allGoods">
        <td>{{ good.name }}</td>
        <td>{{ good.value }}</td>
      </tr>
    </table>
    <hr>
    <button @click="$store.commit('changegoodsname',pages)">为商品改个响亮的名字吧！</button>
    <input type="text" v-model="$store.state.addName">
    <button @click="$store.dispatch('changegoodsname',pages)">换个方式改名~</button>
  </div>
</template>

<script>
export default {
  name: "coffee",
  created(){
    console.log(this.$store);
  },
  data(){
    return{
      pages:"coffee"
    }
  }
}
</script>

<style>

</style>



