<template>
  <div class="salad">
    <div class="salad-up">
      <div class="salad-up-content">
        <h2>蛋蛋</h2>
        <p>其实遇见比所谓天长地久更令人感动，天长地久是用时间衡量的未来，而遇见，是缘分撮合的美妙。</p>
        <p>这场星空之旅我不再孤苦伶仃，孤舟野火，烛残温酒，从春分到冬末， 从日出到日落，从繁星到黎明，我漫无目的地行走云片。因为你在身边，就是我一生的期望。我们的咫尺之隔，仅存梦里。</p>
        <div class="salad-up-content-control">
          <p>
            数&nbsp;&nbsp;量：
            <Counter
              :max="counters.max"
              :min="counters.min"
              :def="counters.def"
              @updata_counter="getcounter"
            />
          </p>
          <p>
            下拉列表：
            <Navlist
              :option="navlists.option"
              :def="navlists.def"
              @updata_navlist="getnavlist"
            />
          </p>
          <p>
            选择钮&nbsp;：
            <Checkboxs
            :option="checkboxss.option"
            :def="checkboxss.def"
            @updata_checkboxs="getcheckboxs"
            />
          </p>
          <p>
            总&nbsp;&nbsp;价：￥
            <span style="font-size:25px;">{{ total }}</span>
          </p>
        </div>
      </div>
    </div>
    <div class="salad-down">
      <div class="salad-down-content">
        <p>荒凉的沙漠，墨色的天穹，满天的繁星，曾经的我们，还有仅剩的我。</p>
        <p>我在未来等你，在夕阳之下，在海浪面前，在爱情时分，在天地之间的见证下，我想对你说：</p>
        <p>我一直都在等你。</p>
      </div>
    </div>
  </div>
</template>

<script>
import Counter from "./components/Counter"
import Navlist from "./components/Navlist"
import Checkboxs from "./components/Checkboxs"

export default {
  name: "salad",
  data(){
    return {
      counters: {
        max: 10,
        min: 3,
        def: 5
      },
      navlists:{
        option:[
          {name:"50元",value:50},
          {name:"100元",value:100},
          {name:"666元",value:666},
          {name:"999元",value:999}
        ],
        def:2
      },
      checkboxss:{
        option:[
          {name:"一倍",value:1},
          {name:"二倍",value:2},
          {name:"三倍",value:3},
          {name:"四倍",value:4}
        ],
        def:2
      },
      counter_num:0,
      navlist_num:0,
      checkboxs_num:0
    }
  },
  computed:{
    total:function(){
      return this.counter_num * this.navlists.option[this.navlist_num].value * this.checkboxss.option[this.checkboxs_num].value
    }
  },
  components: {Counter,Navlist,Checkboxs},
  methods:{
    getcounter(num){
      this.counter_num = num;
    },
    getnavlist(num){
      this.navlist_num = num;
    },
    getcheckboxs(num){
      this.checkboxs_num = num;
    }
  }
}
</script>

<style scoped>
.salad {
  background: rgb(240, 240, 240);
}
.salad-up, .salad-down {
  padding: 20px 20px;
  margin-bottom: 20px;
  box-shadow:1px 2px 14px rgb(205, 209, 218);
}
.salad-up-content,.salad-down-content {
  margin: 0 20px;
}
.salad-up-content h2 {
  text-align: center;
  margin: 0 0 20px 0;
}
.salad-up-content p, .salad-down-content p {
  font-size: 17px;
  text-indent: 2em;
  line-height: 28px;
}
.salad-up-content-control{
  margin: 20px 0 0 0;
}
.salad-up-content-control p{
  margin: 0 0 10px 0;
}
</style>



