<template>
  <div class="counter">
    <span class="counter-btn" @click="sub">-</span>
    <input type="text" class="counter-num" v-model="number" @keyup="inputkeyup" @change="inputchange">
    <span class="counter-btn" @click="add">+</span>
  </div>
</template>
<script>
export default {
  name: "Counter",
  data () {
    return {
      number: this.def
    }
  },
  props: {
    max: {
      type: Number,
      default: 5
    },
    min: {
      type: Number,
      default: 1
    },
    def: {
      type: Number,
      default: function(){
        return this.min
      }
    }
  },
  methods: {
    sub(){
      if(this.number <= this.min){return}
      this.number--;
      this.$emit("updata_counter",this.number);
    },
    add(){
      if(this.number >= this.max){return}
      this.number++;
       this.$emit("updata_counter",this.number);
    },
    inputkeyup(e){
      this.number = this.number.replace(/\D/g,"");
       this.$emit("updata_counter",this.number);
    },
    inputchange(e){
      if(this.number > this.max){
        this.number = this.max;
         this.$emit("updata_counter",this.number);
      }else if(temp < this.min){
        this.number = this.min;
         this.$emit("updata_counter",this.number);
      }
    }
  },
  mounted(){
    this.$emit("updata_counter",this.number);
  }
}
</script>
<style scoped>
.counter {
  display: inline-block;
  text-align: center;
  background-color: #fff;
  border-top: 1px solid #999;
  border-bottom: 1px solid #999;
  margin: 0;
  padding: 0;
  text-indent: 0;
}
.counter-btn{
  display:inline-block;
  width: 1.9rem;
  height: 2.0rem;
  background-color:#ddd;
  border-left: 1px solid #999;
  border-right: 1px solid #999;
  line-height: 2rem;
  cursor: pointer;
  user-select:none;
}
.counter-btn:hover {
  background-color: rgb(8,165,99);
  color: #fff;
}
.counter-num {
  width: 3rem;
  height: 1.6rem;
  border: none;
  outline: none;
  text-align: center;
  margin: 0 -0.45rem 0 -0.45rem;
  font-size: 1.1rem;
}

</style>



